<template>
    <view class="content">
        <!-- <image class="logo" src="../../static/logo.png"></image> -->
        <view class="titletal">
            <text class="title">注册页面</text>
        </view>
            <!--账号-->
            <view class="inputView">
                <image class="nameImage" src="../../static/name.png"></image>
                <label class="loginLab">账号</label>
                <input
                    class="inputText"
                    type="text"
                    placeholder="请输入要注册的账号"
                    value="user"
                    v-model="uname"
                />
            </view>
        <view class="line"></view>
        <!--密码-->
        <view class="inputView">
            <image class="keyImage" src="../../static/key.png"></image>
            <label class="loginLab">密码</label>
            <input
                class="inputText"
                password="true"
                type="text"
                placeholder="请输入密码"
                value="pass"
                v-model="passw"
            />
        </view>
		<view class="line"></view>
		<view class="inputView">
		    <image class="keyImage" src="../../static/key.png"></image>
		    <label class="loginLab">密码</label>
		    <input
		        class="inputText"
		        
		        type="password"
		        placeholder="请再次确认密码"
		        value="passture"
		        v-model="passt"
		    />
		</view>
		<view class="line"></view>
		<view class="inputView">
		    <image class="keyImage" src="../../static/key.png"></image>
		    <label class="loginLab">姓名</label>
		    <input
		        class="inputText"
		        type="text"
		        placeholder="请输入您的姓名"
		        value="name"
		        v-model="namew"
		    />
		</view>
		<view class="line"></view>
		<view class="inputView">
		    <image class="keyImage" src="../../static/key.png"></image>
		    <label class="loginLab">手机</label>
		    <input
		        class="inputText"
		        type="text"
		        placeholder="请输入您的手机号"
		        value="num"
		        v-model="numw"
		    />
		</view>

        <!--按钮-->
		<view class="registerBtnView"><button class="registerBtn" @click="goto()">确认注册</button></view>

 <!--背景图-->      

<!--  <image class="backlogo" src="../../static/BGNew.png"></image>-->
    </view>
</template>

<script>
	export default {
		data(){
			return{
				uname:'',
				passw:'',
				passt:'',
				namew:'',
				numw:''
			};
		},
		mounted(){
			
				
		},
	        methods: {
				goto(){
					var user = {
						account:this.uname,
						password:this.passw,
						name:this.namew,
						phone:this.numw
					}
						if (this.uname.length <= 0 || this.passw.length <= 0) {
							uni.showToast({
							title: '账号或密码不能为空',
						    icon: 'none'
						    });
						    return;
						}
						if(this.namew.length <=0 || this.numw.length <= 0){
							uni.showToast({
								title:'请输入您的姓名和电话号码',
								icon:'none'
							});
							return;
						}
						if (this.passw!=this.passt) {
							uni.showToast({
							title: '请再次确认密码保证一致',
						    icon: 'none'
						    });
						    return;
						}
						if (this.numw.length!=11){
							uni.showToast({
							title: '请确保您的手机号格式正确',
							icon: 'none'
							});
							return;
						}

							wx.request({
							url:'http://192.168.137.1:8081/loginPage/register',
							data:{
								account:this.uname,
								password:this.passw,
								name:this.namew,
								phone:this.numw
							},
							success:(response)=>{
								if(response.data=='OK')
								{
									uni.navigateTo({
										url:'./index'
									})		
								}
								if(response.data=='ERROR')
								{
									uni.showToast({
									title: '该用户名已被注册，请重新输入',
									icon: 'none'
									});
									return;
								}
							}
				})		
	        }
	    }
	}
</script>

